<template>
  <div class="add">
    <h1>Add New Rules</h1>
    <div>
      Add your local rules to help others in your area. For added precision
      while drawing the region, you can visit <u>covid-info.page</u> on your pc
      or mac.
    </div>

    <div class="h2 mt-4">
      Region:
    </div>
    <div id="map-container" @drawend="drawend"></div>
    <span v-if="!drawn" class="text-danger">
      Please draw a region. Click on the map to add a new point to the region's
      border.
    </span>
    <span v-if="drawn"> Area: ~{{ areaText }}km² </span>
    <div class="h2 mt-4">
      Region Name:
    </div>
    <div class="row">
      <div class="input-group col-12 col-md-5">
        <input
          type="text"
          class="form-control"
          placeholder="e.g. Berlin"
          v-model="regionName"
        />
      </div>
      <div class="input-group col-12 col-md-7">
        <div class="input-group-prepend">
          <span class="input-group-text">Type</span>
        </div>
        <div class="input-group-prepend dropup">
          <button
            class="btn btn-outline-secondary dropdown-toggle"
            type="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            {{ regionType == "other" ? "Other:" : "Select ..." }}
          </button>
          <div class="dropdown-menu">
            <a
              class="dropdown-item plain"
              href="javascript:void(0)"
              v-for="t in regionTypes"
              :key="t"
              @click="
                regionType = t;
                customType = titleCase(t);
                customType == 'Other' && (customType = '');
              "
            >
              {{ titleCase(t) }}
            </a>
          </div>
        </div>
        <input
          type="text"
          class="form-control"
          v-model="customType"
          placeholder="e.g. Multi State Area"
          :disabled="regionType !== 'other'"
        />
      </div>
    </div>
    <div class="h2 mt-4">
      Date Range:
    </div>
    <div class="row">
      <div class="input-group col-12 col-md-6">
        <div class="input-group-prepend">
          <span class="input-group-text">
            <font-awesome-icon
              :icon="faCalendar"
              fixed-width
            ></font-awesome-icon>
          </span>
        </div>
        <input
          ref="litepicker"
          type="text"
          class="form-control round-right"
          placeholder="e.g. Jan 1, 2021 - Jan 31, 2021"
        />
      </div>
      <div class="col-12 col-md-6">
        <span v-if="rangeDuration === null" class="text-danger">
          Please select a range.
        </span>
        <span v-if="rangeDuration !== null"> {{ rangeDuration }} days. </span>
      </div>
    </div>
    <div class="h2 mt-4">
      Rules:
    </div>
    <condition-editor v-for="(c, i) in rules" :key="i" :cond="c" class="mb-3" />
    <div class="d-flex">
      <button class="btn btn-primary" @click="addRule">
        <font-awesome-icon :icon="faPlus" />
      </button>
      <span class="expand" /><button class="btn btn-danger" @click="removeRule">
        <font-awesome-icon :icon="faMinus" />
      </button>
    </div>
    <div class="h2 mt-4">
      Source:
    </div>
    <div class="input-group mb-2">
      <div class="input-group-prepend">
        <span class="input-group-text">URL of an official info page</span>
      </div>
      <input
        type="text"
        class="form-control"
        v-model="sourceUrl"
        placeholder="e.g. https://www.rki.de/EN/Content/infections/epidemiology/outbreaks/COVID-19/COVID19.html"
      />
    </div>
    <div class="d-flex">
      <span v-if="!sourceUrl" class="text-danger">Source is required.</span>
      <span class="expand" />
      <button
        class="btn btn-info mb-4"
        :disabled="!completeInputs && false"
        @click="submit"
      >
        Submit via Email
      </button>
    </div>
    <teleport to="body">
      <div
        class="modal fade"
        tabindex="-1"
        role="dialog"
        aria-hidden="true"
        ref="modal"
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">Send submission</h5>
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <p>
                An email dialog should open. If not, you can click
                <a @click="openMail" ref="openMail" href="javascript:void(0)">
                  here</a
                >
                to open it. You can also send the following text to
                <a href="mailto:covid.info.page@gmail.com">
                  covid.info.page@gmail.com</a
                >
                manually:
              </p>
              <textarea
                class="form-control"
                style="height:200px"
                v-model="mailBody"
                onclick="this.focus();this.select()"
                readonly="readonly"
              ></textarea>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </teleport>
  </div>
</template>

<script>
import "ol/ol.css";
import Map from "ol/Map";
import TileLayer from "ol/layer/Tile";
import GeoJSON from "ol/format/GeoJSON";
import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import OSMSource from "ol/source/OSM";
import View from "ol/View";
import Feature from "ol/Feature";
import { circular } from "ol/geom/Polygon";
import { Style, Fill, Circle } from "ol/style";
import Point from "ol/geom/Point";
import { fromLonLat } from "ol/proj";
import { Attribution, defaults as defaultControls } from "ol/control";
import Draw from "ol/interaction/Draw";
import Litepicker from "litepicker";
import moment from "moment";
import { faCalendar, faPlus, faMinus } from "@fortawesome/free-solid-svg-icons";
import ConditionEditor from "@/components/ConditionEditor.vue";
import LocationService from "@/services/location.js";
import { v4 as uuid } from "uuid";

const regionTypes = [
  "neighborhood",
  "city",
  "state",
  "country",
  "continent",
  "other"
];
export default {
  mounted() {
    var attribution = new Attribution({
      collapsible: true
    });
    const source = (this.source = new VectorSource({
      format: new GeoJSON()
    }));
    const map = (this.map = new Map({
      target: "map-container",
      controls: defaultControls({ attribution: false }).extend([attribution]),
      layers: [
        new TileLayer({
          source: new OSMSource()
        }),
        new VectorLayer({
          source
        })
      ],
      view: new View({
        center: [0, 0],
        zoom: 2
      })
    }));

    this.geoSource = new VectorSource();
    const layer = new VectorLayer({
      source: this.geoSource
    });
    this.map.addLayer(layer);
    LocationService.get(this.watchPos);
    const draw = new Draw({
      type: "Polygon",
      source: source
    });
    map.addInteraction(draw);
    draw.on("drawend", this.drawend);
    draw.on("drawstart", this.drawstart);
    new Litepicker({
      element: this.$refs.litepicker,
      singleMode: false,
      format: "MMM D, YYYY",
      onSelect: this.changeRange
    });
    this.addRule();
  },
  methods: {
    openMail() {
      window.open(this.mailToLink);
    },
    handlePasteUrl(url) {
      this.mailBody =
        "If you want to be mentioned by name, put your name here: <anonymous>\n\nKeep the following line!\n" +
        url;
      this.mailToLink = `mailto:covid.info.page@gmail.com?subject=New%20Submission&body=${encodeURIComponent(
        this.mailBody
      )}`;
      window.$(this.$refs.modal).modal("show");
      this.openMail();
    },
    submit() {
      let handle = this.handlePasteUrl;
      let LZMA = window.LZMA;
      let lzma = new LZMA("/paste/lzma_worker.js");
      let plaintext = JSON.stringify(this.generateSendData());
      lzma.compress(plaintext, 1, function(compressed, error) {
        if (error) {
          alert("Failed to compress data: " + error);
          return;
        }

        let reader = new FileReader();
        reader.onload = function() {
          let base64 = reader.result.substr(reader.result.indexOf(",") + 1);
          let url = "https://" + location.host + "/paste/#" + base64;
          handle(url);
        };
        reader.readAsDataURL(new Blob([new Uint8Array(compressed)]));
      });
    },
    defaultRule() {
      return {
        icon: 8,
        color: 9,
        name: "",
        more: ""
      };
    },
    generateSendData() {
      return {
        rules: this.rules,
        extend: this.extend,
        sourceUrl: this.sourceUrl,
        regionName: this.regionName,
        regionType: this.regionType,
        customType: this.customType,
        area: this.area,
        range: this.range,
        dateText: this.$refs.litepicker.value,
        id: uuid()
      };
    },
    changeRange(a, b) {
      a.setMinutes(a.getMinutes() - a.getTimezoneOffset());
      b.setMinutes(b.getMinutes() - b.getTimezoneOffset());
      this.range = [a, b];
      this.rangeDuration = null;
      if (!(a && b)) return;
      a = moment(a);
      b = moment(b);
      this.rangeDuration = b.diff(a, "days") + 1;
    },
    addRule() {
      this.rules.push(this.defaultRule());
    },
    removeRule() {
      this.rules.length > 1 && this.rules.pop();
    },
    drawend() {
      setTimeout(() => {
        this.drawn = true;
        this.extend = this.source
          .getFeatures()[0]
          .getGeometry()
          .getExtent();
        this.area = this.source
          .getFeatures()[0]
          .getGeometry()
          .getArea();

        console.log(
          LocationService.contained(
            LocationService.posToPoint(LocationService.current.coords),
            this.extend
          )
        );
      }, 1);
    },
    drawstart() {
      this.source.clear();
      this.extend = null;
      this.area = 0;
      this.drawn = false;
    },
    titleCase(s) {
      return s.replace(/(?:^|\s|-)\S/g, x => x.toUpperCase());
    },
    watchPos(pos) {
      const coords = pos.coords;
      const accuracy = circular(coords, pos.accuracy);
      this.geoSource.clear(true);
      let fs = [
        new Feature(
          accuracy.transform("EPSG:4326", this.map.getView().getProjection())
        ),
        new Feature(new Point(fromLonLat(coords)))
      ];
      fs = fs.map((v, i) => {
        v.setStyle(
          new Style({
            fill: new Fill({
              color: "#ff000010"
            })
          })
        );
        i &&
          v.setStyle(
            new Style({
              image: new Circle({
                radius: 5,
                fill: new Fill({ color: "#ff0000aa" })
              })
            })
          );
        return v;
      });
      this.geoSource.addFeatures(fs);
      if (!this.viewSet) {
        this.viewSet = true;
        this.map.setView(
          new View({
            center: fromLonLat(coords),
            zoom: 7
          })
        );
      }
    }
  },
  data() {
    return {
      geoSource: null,
      map: null,
      coords: null,
      area: 0,
      source: null,
      sourceUrl: null,
      date: null,
      rangeDuration: null,
      range: [null, null],
      faCalendar,
      faPlus,
      faMinus,
      drawn: false,
      regionName: null,
      regionTypes,
      regionType: regionTypes[1],
      customType: this.titleCase(regionTypes[1]),
      viewSet: false,
      rules: [],
      mailToLink: null,
      mailBody: null
    };
  },
  components: { ConditionEditor },
  computed: {
    areaText() {
      return Math.round(((this.area * 0.57) / 1000 / 1000) * 100) / 100;
    },
    completeInputs() {
      return (
        this.drawn &&
        this.rangeDuration !== null &&
        this.sourceUrl &&
        this.regionName
      );
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
