#map-container {
  margin: 0;
  height: 20rem;
  width: 100%;
  font-family: sans-serif;
  background-color: #04041b;
}

.add {
  text-align: left;
}

.round-right {
  border-radius: 0 0.25rem 0.25rem 0 !important;
}
</style>
