<template>
  <div class="host">
    <div class="row">
      <div class="dropdown mb-1 col-6 col-md-3">
        <button
          class="btn btn-light dropdown-toggle"
          type="button"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          Color:
          <font-awesome-icon
            :icon="faSquareFull"
            :style="{ color: colors[condition.color] }"
          />
          <font-awesome-icon
            :icon="faSquareFull"
            :style="{ color: colors[condition.color] }"
          />
          <font-awesome-icon
            :icon="faSquareFull"
            :style="{ color: colors[condition.color] }"
          />
        </button>
        <div class="dropdown-menu">
          <a
            class="dropdown-item plain"
            href="javascript:void(0)"
            v-for="(c, i) in allowedColors"
            :key="'c' + i"
            @click="condition.color = colors.indexOf(c)"
          >
            Color:
            <font-awesome-icon :icon="faSquareFull" :style="{ color: c }" />
            <font-awesome-icon :icon="faSquareFull" :style="{ color: c }" />
            <font-awesome-icon :icon="faSquareFull" :style="{ color: c }" />
          </a>
        </div>
      </div>
      <div class="dropdown mb-1 col-6 col-md-2">
        <button
          class="btn btn-light dropdown-toggle"
          type="button"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          Icon:
          <font-awesome-icon
            :icon="icons[condition.icon]"
            :key="condition.icon"
          />
        </button>
        <div class="dropdown-menu">
          <a
            class="dropdown-item plain"
            href="javascript:void(0)"
            v-for="(c, i) in allowedIcons"
            :key="'i' + i"
            @click="condition.icon = icons.indexOf(c)"
          >
            Icon:
            <font-awesome-icon :icon="c" />
          </a>
        </div>
      </div>
      <div class="input-group mb-1 col-12 col-md-7">
        <input
          type="text"
          class="form-control"
          placeholder="e.g. Curfew at 9pm"
          v-model="condition.name"
        />
      </div>
    </div>
    <textarea
      class="form-control mt-2"
      placeholder="e.g. All residents have to stay home after 9pm."
      style="height: 100px"
      v-model="condition.more"
    ></textarea>
  </div>
</template>

<script>
import globals from "@/components/globals.js";
import { faSquareFull } from "@fortawesome/free-solid-svg-icons";
const $ = window.$;

export default {
  props: ["cond"],
  data() {
    return {
      shown: false,
      colors: globals.colors,
      icons: globals.icons,
      condition: this.cond,
      faSquareFull
    };
  },
  computed: {
    allowedColors() {
      return this.colors.slice(7);
    },
    allowedIcons() {
      return this.icons.slice(7);
    }
  },
  methods: {
    toggle() {
      $(this.$refs.accordion).collapse("toggle");
    }
  },
  watch: {
    cond(val) {
      this.condition = val;
    }
  }
};
</script>

